
.arrow {
  display: flex;
    align-items: center;
    cursor: pointer;
    z-index: 200;
}



#right-arrow {
    width: 200px;
    height: 60px;
    background: white;
    opacity: .6;
    position: fixed;
    top: 45%;
    right:-150px;
    cursor: pointer;
    }

#right-arrow:hover, #left-arrow:hover {
    opacity: 1;
    background-color:rebeccapurple;
}

#right-arrow:hover .arrow-svg path, #left-arrow:hover .arrow-svg path{
    fill:white;
}

#left-arrow {
    width: 200px;
    height: 60px;
    background: white;
    opacity: .6;
    position: fixed;
    top: 45%;
    left:-150px;
    text-align: right;
    cursor: pointer
    }


#left-arrow-svg{
    position: absolute;
    right:10px;
    top:13px;
}
#right-arrow-svg{
    margin-left: 10px;
}
#right-project {
    margin-left: 20px;
    margin-right: 10px;
    }

.left-project {
    width: 150px;
    margin: 0;
    padding: 0;
    position: absolute;
    right:60px;
    top: 0;
    }

#left-project-taylor-fitness-coaching {
    top: -10px;
}

.project-arrow-a {
    color: black;
}

.arrow-svg path {
    fill: black;
}

.right-arrow-h5, .left-arrow-h5 {
    color:white;
}

.left-arrow-h5 {
    width: 150px;
    }



.right-arrow-h5:hover, .left-arrow-h5:hover {
    opacity: .7;
}








/********************** HEADER*******************/

.top-of-header-container{
    width: 100%;
    background: white;
    height: 95px;
    display: flex;
    align-items: center;
}

.top-of-header-logo-container{
    margin-left: 10px;
    text-align: center;
}



.project-overlay {
    position: absolute;
    width:100%;
    height:100%;
    top: 0;
    left:0;
    bottom: 0;
    right: 0;
    background-color: black;
    opacity: 0;
    z-index: 105;
    }

.project-container {
    width: 90%;
    height: auto;
    margin: 0 auto;
    
}

.project-heading-h1 {
    margin-bottom: 0;
}

.project-heading-h5 {
    margin-top: 0;
    color: rebeccapurple;
}

.project-heading-h3{
    margin-top: 0;
    margin-bottom: 100px;
    display: inline-block;
    color: darkgray;
    font-style: italic;
    font-weight: 100;
    border-bottom: .5px solid darkgray;
}



.project-description {
    margin-bottom: 50px;
}

.project-description-containers {
    
}

.project-intro-title {
    color:black;
}

#project-details-p {
    width: 200px;
}

.project-intro-p {
    color: darkgray;
    font-size: 12px;
}
.bottom-project-container {
    width: 300px;
    height: auto;
    margin: 0 auto;
    display: block;
   }
.bottom-project-container-right{
    width: 100%;
    height:auto;
    
}

.bottom-project-container-left {
    height:300px;
    width:100%;
    background-position: center;
    background-repeat: no-repeat;
    background-size:cover;
     border: 1px solid $gray-color;
    
    }

.bottom-project-container-right-top {
    height:300px;
    width:100%;
    background-position: top center;
    background-repeat: no-repeat;
    background-size:cover;
     border: 1px solid $gray-color;
    
}

.bottom-project-container-right-bottom {
    height:300px;
    width:100%;
    background-position: top center;
    background-repeat: no-repeat;
    background-size:cover;  
     border: 1px solid $gray-color;
}
.project-image-container {
    @include header-image-container;
     @include project-container;
    }



